import React from 'react';
import {Container, Row} from 'react-bootstrap';

import Card from '../components/Card';


import weather from '../assets/images/weather.jpg';
import coder from '../assets/images/ben-2.jpg';
import pizza from '../assets/images/pizza.jpg';


class Carousel extends React.Component {
    constructor(props) {
        super (props);
        this.state = {
           items: [
               {
                   id: 0,
                   title: 'REACT Weather App',
                   subTitle:'Find your local weather',
                   imgSrc: weather,
                   link:'https://bmeena57.github.io/deploy_react_weather/',
                   selected:false
               },
               {
                    id: 1,
                    title: 'Benjamin',
                    subTitle: 'The Coder',
                    imgSrc: coder,
                    link:'https://github.com/BMeeNA57',
                    selected:false
               },
               {
                    id: 2,
                    title: 'JavaScript Powered - Pizza Menu - ',
                    subTitle: 'Focusing on Clean & Logical Code',
                    imgSrc: pizza,
                    link:'https://github.com/BMeeNA57/JavaScript-Projects/tree/master/Pizza%20Project',
                    selected:false
               },
           ]
        }
    }

    handleCardClick = (id, card) => {
        console.log(id);

        let items = [...this.state.items];

        items[id].selected = items[id].selected ? false : true;

        items.forEach(item => {
            if(items.id !== id) {
                items.selected = false;
            }
        });

        this.setState({
            items
        });
    }


    makeItems = (items) => {
        return items.map(item => {
            return <Card item={item} click={(e => this.handleCardClick(item.id, e))} key={item.id} />
        })
    }


    render() {
        return(
            <Container fluid={true}>
                <Row className="justify-content-around">
                    {this.makeItems(this.state.items)}
                </Row>
            </Container>
        );
    }
}

export default Carousel;