import React from 'react';
import Hero from '../components/Hero';
import Content from '../components/Content';
import {Row, Col} from 'react-bootstrap';
import Axios from 'axios';



class ContactPage extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            name: '',
            email: '',
            message: '',
            disabled: false,
            emailsent: null,
        }
    }

    handleChange = (event) => {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;

        this.setState({
            [name]:value
        })
    }

    handleSubmit = (event) => {
            event.preventDefault();

            console.log(event.target)

            this.setState ({
                disabled: true
            });

            Axios.post('http://localhost:3030/api/email', this.state)
                .then(res => {
                    if(res.data.success) {
                        this.setState({
                            disabled: false,
                            emailSent: true 
                         });
                    } else {
                        this.setState({
                            disabled: false,
                            emailSent: false 
                         });
                    }
                   
                })
                .catch(err => {
                    console.log(err);

                    this.setState({
                        disabled: false,
                        emailSent: false
                    });
                })

    }

    render() {
         return(
            <div>
                <Hero title={this.props.title} />

                <Content>
                    <Row className="md-6">
                        <Col>
                       <p>            
                        <b>EXPERIENCE</b>
                        <hr></hr>
                        <b>Prosper IT Consulting, Denver — Web Developer </b>
                        <br></br>
                        November 2019 - April 2020
                        <br></br>
                        <br></br>
                        SCRUM and Agile based methodology.
                        Markup languages HTML5 and CSS were used to create visually appealing and professional appearances.
                        Dispatched Bootstrap 4 framework for  consistent content design.  
                        Google Meet, Slack, and Azure were main communication tools.
                        Visual Studio Community and Visual Studio Code were used as IDE’s.
                        Exposure to C#, C++, and Razor
                        MVC experience and implementation.
                        <hr></hr>
                        <b>5 Guys Enterprise, Greenwood Village — Manager </b>
                        <br></br>
                        November 2019 - Present
                        <br></br>
                        <br></br>
                        Ensure that the Occupational Safety and Health Act, local health and safety codes, and company safety and security policies are followed. 
                        Assist with recruiting, interviewing, and hiring new crew members.
                        Assist General Manager inday to day operation with tasks including scheduling, ordering, and team development.

                       </p>
                    <br></br>
                    <br></br>
                    </Col>
                    <Col className="md-6">

                    <b>EDUCATION</b>
                    <hr></hr>
                    <b>The Tech Academy- Denver, Colorado</b>			
                    <br></br>
                    	2019
                    <br></br>
                    <br></br> 
                    Comprehensive overview of Computer Science and software development with courses in HTML5, CSS3, JavaScript/jQuery, MVC, Version Control, and Agile product development methods (Scrum).
                    <br></br>
                    <br></br>
                    <br></br>
                    <hr></hr>
                    <b>University of Missouri - Saint Louis, MO</b>			
                    <br></br>
                    	2015
                    <br></br>
                    <br></br>
                    Mass Communication
                    <hr></hr>

                    <b>Contact Info</b>
                    <br></br>
                    <tb>
                        <tr>Benjamin Meena</tr>
                        <tr>ben.meena16@gmail.com</tr>
                        <tr>757.234.5481</tr>
                    </tb>
                    </Col>
                    </Row>
        
                </Content>
            </div>
        );
    }
}

export default ContactPage;
